<template>
	<view>

		<view class="" style="text-align: center;">
			<uni-card>
				<text>---UserInfo_Account---默认禁用，自动生成</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_ID---不需要，数据库自动生成</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_Name---必填字段</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_IdentiyCard---必填字段</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_Sex---由身份证自动获取性别</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_Class---默认禁用</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_TelNumber---非必填字段</text>
			</uni-card>

			<uni-card>
				<text>---UserInfo_QQNumber---非比填字段</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_WechatNumber---非比填字段</text>
			</uni-card>
			<uni-card>
				<text>---UserInfo_MainNumber---非必填字段</text>
			</uni-card>

		</view>

		<view class="view_Btn">
			<uni-card title="" thumbnail="" extra="" note="">
				<uni-button @click="goPost()">提交</uni-button>
			</uni-card>
		</view>

		<view class="view_Test" style="text-align: center;">
			<uni-card>
				<text>---其他功能占位---</text>
			</uni-card>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 设置本地信息绑定input
			};
		},
		methods: {
			setStorage: function(bool, obj) {
				uni.setStorage({
					key: 'hasLogin',
					data: bool,
					success: function() {
						console.log('sethasLogin-YES');
					}
				});

				uni.setStorage({
					key: 'UserInfo',
					data: obj,
					success: function() {
						console.log('setUserInfo-YES');
					}
				});

				if (bool) {
					console.log("TOMY");
					uni.switchTab({
						url: "/pages/my/my",
					})
				}
			},

			goPost: function() {
				// 获取绑定inout数据
				// 提交函数，POST请求
				console.log("goPost");



				// 完善,POST成功后
				if (true) {
					// 使用本地绑定input数据创建setStorage对象
					let obj = {
						UserInfo_Account: "2131010414",
						UserInfo_Class: "test",
						UserInfo_ID: 0,
						UserInfo_IdentityCard: "test",
						UserInfo_MailNumber: "test",
						UserInfo_Name: "test",
						UserInfo_QQNumber: "test",
						UserInfo_Sex: "test",
						UserInfo_TelNumber: "test",
						UserInfo_WechatNumber: "test"
					}
					// setStorage设置本地数据并跳转MY页
					this.setStorage(true, obj)
				}
			}
		}
	}
</script>

